a {
  text-decoration: none;
  color: black;
}

.navbar__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  height: 40px;
  font-family: Ariel;
  color: black;
  font-size: 25px;
  background-color: #f4f3ea;
  padding: 12px;
}
.positionFixed {
  position: fixed;
  width: 100%;
}

.navbar__image {
  width: 40vh;
}

.linksTest {
  display: flex;
  justify-content: space-around;
  width: 600px;
}

@media screen and (max-width: 490px) {
  .navbar__container {
    justify-content: space-evenly;
    font-size: 12px;
    padding: 0px;
  }
  .navbar__image {
    width: 15vh;
  }
}

@media screen and (max-width: 1000px) and (min-width: 601px) {
    .navbar__container {
      justify-content: space-between;
      font-size: 20px;
      padding: 0px;
    }
    .navbar__image {
      width: 30vh;
    }
  }


  @media screen and (max-width: 600px) and (min-width: 500px) {
    .navbar__container {
      justify-content: space-between;
      font-size: 15px;
      padding: 0px;
    }
    .navbar__image {
      width: 30vh;
    }
  }


@media screen and (max-width: 1600px) and (min-width: 1001px) {
  .navbar__container {
    justify-content: space-between;
    font-size: 25px;
    padding: 0px;
  }
  .navbar__image {
    width: 30vh;
  }
}

@media screen and (max-width: 912px) and (min-width: 900px) {
  .navbar__section {
    font-size: 3vh;
  }
}