.projects__background {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-attachment: fixed;
  height: 100%;
  background: #0d0d0d

  ;
  padding-bottom: 40px;
}

.projects__grid-main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 20px;
  column-gap: 20px;
}

.projects__image-config {
  width: 60vh;
  height: 60vh;
}

.projects__title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh;
  font-size: 8vh;
  font-family: Iskry;
  color: white;
}


@media only screen and (max-width: 490px) {
    .projects__image-config {
        width: 42vh;
        height: 42vh;

    }

}
